import React from "react";
import "../scss/Navbar.scss";
import Search from "./Search";
import { Link } from "react-router-dom";

function Navbar() {
  const activateHome = () => {
    const home = document.querySelector(".home");
    const about = document.querySelector(".about");

    if (!home.classList.contains("active-page")) {
      home.classList.add("active-page");
      about.classList.remove("active-page");
      document.querySelector(".search-field").style.visibility = "visible";
    }
    showNav();
  };
  const activateAbout = () => {
    const about = document.querySelector(".about");
    const home = document.querySelector(".home");
    if (!about.classList.contains("active-page")) {
      about.classList.add("active-page");
      home.classList.remove("active-page");
      document.querySelector(".search-field").style.visibility = "hidden";
    }
    showNav();
  };

  const showNav = () => {
    const menu = document.querySelector(".menu-list");
    let transform, opacity;
    if (menu.style.opacity == 1) {
      transform = "translateY(-50%)";
      opacity = "0";
    } else {
      transform = "translateY(0%)";
      opacity = "1";
    }
    menu.style.transform = transform;
    menu.style.opacity = opacity;
  };

  return (
    <nav className="nav-bar">
      <div className="branding">
        <Link to="/">
          <h1 onClick={activateHome}>Recipe App</h1>
        </Link>
      </div>
      <div className="search-field">
        <Search />
      </div>
      <div className="menu">
        <ul className="menu-list">
          <Link to="/">
            <li className="home active-page" onClick={activateHome}>
              Home
            </li>
          </Link>
          <Link to="/about">
            <li className="about" onClick={activateAbout}>
              About
            </li>
          </Link>
        </ul>
      </div>
      <div className="burger" onClick={showNav}>
        <img src={require("../img/burger-black.png")} alt="burger" />
      </div>
    </nav>
  );
}

export default Navbar;
