import React from "react";
import RecipeStyle from "../scss/Recipe.module.scss";

function Recipe({ title, calories, ingredients, image }) {
  return (
    <div className={RecipeStyle.container}>
      <h1 id="title">{title}</h1>
      <h5 className="calories">{Math.round(calories)} Calories</h5>
      <ol>
        {ingredients.map(ingredient => (
          <li>
            <p className="ingridients">{ingredient.text}</p>
          </li>
        ))}
      </ol>
      <img src={image} alt="no available" />
    </div>
  );
}

export default Recipe;
