import React from "react";
import "../scss/About.scss";

function About() {
  return (
    <div className="about">
      <h1>
        <strong>Welcome to about page</strong>
      </h1>
      <h3>
        <i>
          My First Single Page Application using <strong>React</strong> with
          <strong> API Request</strong>, <strong>SCSS</strong>, and
          <strong> Bulma</strong>.
        </i>
      </h3>
      <h4>
        Single Page Application using <strong>React Router</strong>
        <br />
        Search Field using <strong>Bulma</strong>
        <br />
        Style using <strong>SCSS</strong>
        <br />
        and
        <br />
        Recipe List using <strong>API Request</strong> from edamam.com
      </h4>
    </div>
  );
}

export default About;
